import React, { useRef, useState } from "react";
function App() {
  const [breakLength, setBreakLength] = useState(5);
  const [sessionLength, setSessionLength] = useState(25);
  const [session, setSession] = useState(25 * 60);
  const [isCounting, setIsCounting] = useState(false);
  const [timerId, setTimerId] = useState();
  const [isBreak, setIsBreak] = useState(false);
  const isBreakRef = useRef(isBreak);
  const [sessionColor, setSessionColor] = useState("#edeef7");
  const sessionColorRef = useRef(sessionColor);
  const [sessionText, setSessionText] = useState("Session");
  const sessionTextRef = useRef(sessionText);

  function changeText(newText) {
    sessionTextRef.current = newText;
    setSessionText(newText);
  }

  function changeColor(newColor) {
    sessionColorRef.current = newColor;
    setSessionColor(newColor);
  }
  function updateBreakState(newState) {
    isBreakRef.current = newState;
    setIsBreak(newState);
  }

  function numToTime(sec) {
    var minutes = Math.floor(sec / 60);
    var seconds = sec % 60;

    if (minutes < 10) {
      minutes = "0" + minutes;
    }
    if (seconds < 10) {
      seconds = "0" + seconds;
    }

    return minutes + ":" + seconds;
  }

  function handlerBreakDecrement() {
    if (breakLength > 1) {
      setBreakLength(breakLength - 1);
    }
  }

  function handlerBreakIncrement() {
    if (breakLength <= 59) {
      setBreakLength(breakLength + 1);
    }
  }

  function handlerSessionDecrement() {
    if (sessionLength > 1) {
      setSessionLength(sessionLength - 1);
      setSession(session - 60);
    }
  }

  function handlerSessionIncrement() {
    if (sessionLength <= 59) {
      setSessionLength(sessionLength + 1);
      setSession(session + 60);
    }
  }

  function startCountdown() {
    if (isCounting === false) {
      setTimerId(
        setInterval(() => {
          setSession((prevSession) => {
            if (prevSession >= 1 && prevSession <= 61) {
              //1 & 61 instead of 0 & 60 because returns prevSession - 1 at the end.
              changeColor("#FF6767");
            } else {
              changeColor("#edeef7");
            }
            if (prevSession === 0) {
              let audio = document.getElementById("beep");
              audio.play();

              if (isBreakRef.current) {
                setSession(sessionLength * 60);
                changeText("Session");
                updateBreakState(false);
              } else {
                setSession(breakLength * 60);
                changeText("Break");
                updateBreakState(true);
              }
            }
            return prevSession - 1;
          });
        }, 1000)
      );

      setIsCounting(true);
    }
  }

  function pauseCountdown() {
    if (isCounting === true) {
      clearInterval(timerId);
      setIsCounting(false);
    }
  }

  function startPauseOnClick() {
    if (isCounting) {
      pauseCountdown();
    } else {
      startCountdown();
    }
  }

  function resetCountdown() {
    setBreakLength(5);
    setSessionLength(25);
    setSession(25 * 60);
    clearInterval(timerId);
    setIsCounting(false);
    updateBreakState(false);
    changeText("Session");
    let audio = document.getElementById("beep");
    audio.pause();
    audio.currentTime = 0;
  }

  return (
    <div>
      <div className="app-container">
        <div className="app-title"> 25 + 5 Clock</div>
        <div className="length-container">
          <div className="break-length">
            <div id="break-label">Break Length</div>
            <div className="break-box">
              <i
                id="break-decrement"
                onClick={handlerBreakDecrement}
                className="fas fa-arrow-circle-down icons"
              ></i>
              <div id="break-length">{breakLength}</div>
              <i
                id="break-increment"
                onClick={handlerBreakIncrement}
                className="fas fa-arrow-circle-up icons"
              ></i>
            </div>
          </div>
          <div className="session-length">
            <div id="session-label">Session Length</div>
            <div className="session-box">
              <i
                id="session-decrement"
                onClick={handlerSessionDecrement}
                className="fas fa-arrow-circle-down icons"
              ></i>
              <div id="session-length">{sessionLength}</div>
              <i
                id="session-increment"
                onClick={handlerSessionIncrement}
                className="fas fa-arrow-circle-up icons"
              ></i>
            </div>
          </div>
        </div>
        <div className="session" style={{ color: sessionColor }}>
          <div id="timer-label">
            {sessionText} <br />
          </div>
          <div id="time-left">{numToTime(session)}</div>
        </div>
        <div>
          <div
            id="start_stop"
            onClick={startPauseOnClick}
            className="start_stop far"
          >
            <i className="buttons far fa-play-circle"></i>
            <i className="buttons far fa-pause-circle"></i>
          </div>
          <i
            id="reset"
            onClick={resetCountdown}
            className="buttons fas fa-redo-alt"
          ></i>
        </div>
        <div className="footer">Coded by @7ing7ing</div>
        <audio
          id="beep"
          src="https://assets.mixkit.co/sfx/preview/mixkit-repeating-arcade-beep-1084.mp3"
        ></audio>
      </div>
    </div>
  );
}

export default App;
